<template>
  <div class="shop-car">
    <ul class="shop-car-list">
      <li class="shop-car-item" v-for="(item,index) in cart_list">
        <label class="left-content">
          <input type="checkbox" :value="item" v-model="check_goods">
          <img class="shop-car-img" :src="item.goods_img" alt>
        </label>
        <div class="right-content">
          <p class="name">{{item.goods_name}}</p>
          <p class="desc">{{item.goods_desc}}</p>
          <p class="type">{{item.goods_type}}</p>
          <p class="delete" @click="deleteBtn()">删除</p>
          <div class="price-group">
            <p class="price">￥{{item.goods_price}}</p>
            <div class="num-box">
              <input type="button" value="-" @click="reduceBtn(item)" class="reduce-btn">
              <input type="text" class="num-val" :value="item.num">
              <input type="button" value="+" @click="addBtn(item)" class="add-btn">
            </div>
          </div>
        </div>
      </li>
    </ul>
    <div class="shop-car-payment" v-if="cart_list.length > 0 ">
      <p class="left-content">
        <span class="payment">付款</span>
        <span class="price-num">￥{{total_price}}</span>
        <span class="delivery-type">免费配送</span>
      </p>
      <!-- <p class="right-content" >
        <span>去结算</span>
      </p>-->
      <input
        :class="check_goods.length <= 0 ? 'right-content err-class': 'right-content success-class'"
        @click="payment()"
        type="button"
        :disabled="check_goods.length <= 0"
        name
        id
        value="去结算"
      >
    </div>
    <div class="no-shop-car" v-else="cart_list.length>0">
          <img src="../../common/images/kongche.png" alt="">
    </div>
    <van-popup v-model="show" class="alert-tip-box">
        <div class="alert-tip">
            <h2 class="title">您确认删除该商品吗?</h2>
            <div class="operation">
                <p class="cancel" @click="cancel()">取消</p>
                <p class="confirm" @click="confirm()">确认</p>
            </div>
        </div>
    </van-popup>
  </div>
</template> 

<script>
import { MessageBox } from "mint-ui";
export default {
  data() {
    return {
      show:false,
      deleteStatus:false,
      cart_list: [
        {
          goods_name: "单鞋深度翻新养护",
          goods_desc: "养护周期15—20个工作日",
          goods_price: "100",
          goods_type: "單鞋",
          goods_img:
            "http://baodashi-10002130.image.myqcloud.com/609ab87a-0804-4fe5-9798-ab9a959d0823",
          num: "2"
        },
        {
          goods_name: "短靴深度翻新养护",
          goods_desc: "靴筒高<20cm",
          goods_price: "200",
          goods_type: "長靴",
          goods_img:
            "http://baodashi-10002130.image.myqcloud.com/f095f686-c9d7-4556-ab0a-01aff881dc45",
          num: "2"
        },
        {
          goods_name: "钱包深度翻新养护",
          goods_desc: "对角线<23cm",
          goods_price: "300",
          goods_type: "錢包",
          goods_img:
            "http://baodashi-10002130.image.myqcloud.com/4eae31ab-347f-46c1-9026-895bf49ddd74",
          num: "2"
        }
      ],
      check_goods: [] //已选择的商品
    };
  },
  computed: {
    //总价
    total_price() {
      let price = 0;
      this.check_goods.forEach(item => {
        // 总价 = 价格 * 数量
        price += Number(item.goods_price) * Number(item.num);
      });
      return price;
    },
    //数量
    total_num() {
      let t_num = 0;
      this.check_goods.forEach(item => {
        t_num += Number(item.num);
      });
      return t_num;
    }
  },
  methods: {
    reduceBtn(item) {
      if (item.num <= 0) {
        item.num = 0;
      } else {
        item.num--;
      }
    },
    //              商品累加
    addBtn(item) {
      item.num++;
    },
    //              删除商品
    deleteBtn(item) {
      this.show=true;
      this.check_goods.splice(this.check_goods.indexOf(item), 1);
       this.cart_list.splice(this.cart_list.indexOf(item), 1);
      
    },
    //全选
    check_all() {
      if (this.check_goods.length > 0) {
        this.check_goods = [];
      } else {
        this.cart_list.forEach(item => {
          this.check_goods.push(item);
        });
      }
    },
    //确认按钮
    confirm(){
       
    },

    //取消按钮
    cancel(){
       this.show=false;
    },
    //结算
    payment() {
    //   MessageBox.confirm("", {
    //     message: "您确认删除这个商品吗?",
    //     title: "提示",
    //     confirmButtonText: "取消",
    //     cancelButtonText: "确认"
    //   })
    //     .then(action => {
    //       if (action == "confirm") {
    //         //确认的回调
    //         console.log(1);
    //       }
    //     })
    //     .catch(err => {
    //       if (err == "cancel") {
    //         //取消的回调
    //         console.log(2);
    //       }
    //     });
    }
  }
};
</script>

<style lang="less" scoped>
//购物车样式
.shop-car {
  .shop-car-list {
    .shop-car-item {
      display: flex;
      align-items: center;
      padding: 20px;
      border-bottom: 1px solid #fefefe;
      background: #fff;
      .left-content {
        width: 308px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        input[type="checkbox"] {
          width: 30px;
          height: 30px;
        }
        .shop-car-img {
          width: 212px;
        }
      }
      .right-content {
        width: 362px;
        padding: 0 30px;
        position: relative;
        .name {
          font-size: 28px;
          color: #333;
          margin-bottom: 10px;
        }
        .desc {
          font-size: 24px;
          color: #b3b3b3;
          margin-bottom: 16px;
        }
        .type {
          font-size: 24px;
          color: #b3b3b3;
          display: inline-block;
          border: 1px solid #b3b3b3; /*no*/
          padding: 8px 16px;
          border-radius: 10px;
          margin-bottom: 10px;
        }
        .delete {
          position: absolute;
          right: 0;
          top: 0;
          font-size: 24px;
          color: #aaa;
        }
        .price-group {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 36px;
          .price {
            color: #f7666a;
          }

          .num-box {
            .reduce-btn {
              width: 56px;
              height: 56px;
              border-radius: 50%;

              background: #fff;
              border: 1px solid #e0e0e0;
              outline: none;
            }
            .num-val {
              width: 60px;
              margin: 0 10px;
              outline: none;
            }
            .add-btn {
              width: 56px;
              height: 56px;
              border-radius: 50%;
              background: #fff;
              border: 1px solid #e0e0e0;
              outline: none;
            }
          }
        }
      }
    }
  }
}

//购物车支付样式
.shop-car-payment {
  width: 100%;
  height: 100px;
  position: fixed;
  display: flex;
  bottom: 100px;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  font-size: 32px;
  font-weight: 500;
  .left-content {
    color: #333;
    .payment {
      margin-left: 60px;
    }
    .price-num {
      color: #f76669;
    }
    .delivery-type {
      font-size: 24px;
      margin-left: 32px;
    }
  }
  .right-content {
    width: 220px;
    color: #fff;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .success-class {
    background: #f76669;
  }
  .err-class {
    background: #e0e0e0;
  }
}
.mint-msgbox{
    width: 80% !important;
    margin: 0 auto;
}

//购物车为空时的样式
.no-shop-car{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    img{
        width: 100%;
        
    }
}
.alert-tip-box{
    border-radius: 10px;
    .alert-tip{
        width: 400px;
        padding:40px 30px;
        .title{
           font-size: 30px;
           text-align: center;
        }
        .operation{
           width: 100%;
           display: flex;
           text-align: center;
           font-size: 30px;
           margin-top: 40px;
           p{
               width: 50%;
           }
        }
    }
}

</style>




